body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root{
    width: 100%;
    height: 100%;
}

.aAbout{
    width: 100%;
}
.line{
    width: 100%;
    height: 500px;
}
.aHome{
    width: 100%;
}
.news{
    width: 100%;
}
.pie{
    width: 100%;
    height: 500px;
}
.App {
    width: 100%;
    height: 100%;
    position: relative;
}
.aHeader{
    position: absolute;
    width: 100%;
    height: 60px;
    top: 0;
    left: 0;
    background-color: #096dd9;
    color: #fff;
}
.h_left{
    width: 90%;
    height: 60px;
    line-height: 60px;
    padding-left: 100px;
    font-size: 20px;
    float: left;
}
.h_right{
    width: 10%;
    height: 60px;
    line-height: 60px;
    float: left;
    text-align: center;
}
.aHeader p{
    line-height: 60px;
}
.aSide{
    position: absolute;
    top: 60px;
    left: 0;
    bottom: 0;
}

.aCont{
    position: absolute;
    top: 60px;
    left: 250px;
    bottom: 0;
    right: 0;
}

.aBreadcrumb{
    height: 50px;
    margin: 10px 20px;
    padding: 0 20px;
    border: 1px solid #f5f5f5;
    border-radius: 5px;
    box-shadow: 3px 3px 4px #f5f5f5;
}
.Content{
    margin: 10px 20px;
    padding: 10px;
    border: 1px solid #f5f5f5;
    border-radius: 10px;
    box-shadow: 3px 3px 4px #f5f5f5;
}




